import React from 'react'
import "../styles/prof.css"
import { useState } from 'react'


import Header from '../components/Header'
import Footer from '../components/Footer'
import ScrollButton from '../components/ScrollButton'
import Quiz from '../components/Proforient/Quiz'
import { VKShareButton } from 'react-share'
import { IoShare } from 'react-icons/io5'

export default function ProfOrient () {



    const [isOpen, setOpen] = useState(false);
    const shareUrl = 'atlas.krtsc.ru/proforient'

    const refreshPage = ()=>{
      window.location.reload();
   }


  
  return (
    <div>
      <title></title>
      <Header />
      <ScrollButton />
    <div className='cnt'>
    <h1> </h1>
    <h2>   ,      </h2>

    <div className="quiztime">
      <p>15 </p>
      <p>~10 </p>
    </div>

    <div>
        <button onClick={() => setOpen(!isOpen)}></button>
      </div>
    
    </div>

      <div className={`quiz ${isOpen ? "quiz___active" : ""}`}>
        <div className="quizzes">
          <div className="quizzname"><h1> </h1><div className="btnsquiz"><VKShareButton title='' url={shareUrl}><span className='material-symbols-outlined' style={{paddingRight: 7}}>share</span></VKShareButton><span className='material-symbols-outlined' onClick={refreshPage}>close</span></div></div>
          <div className="quizcont">
          <Quiz />
           </div>
        </div>
      </div>


    <Footer />
    </div>
  )
}



